<template>
	<div class="ratingselect">
		<div class="rating-type border-1px">
			<span @click="select(2,$event)" class="block positive" :class="{'active':selectType===2}">{{desc.all}} <span class="count">{{ratings.length}}</span></span>
			<span @click="select(0,$event)" class="block positive" :class="{'active':selectType===0}">{{desc.positive}} <span class="count">{{positive.length}}</span></span>
			<span @click="select(1,$event)" class="block negative" :class="{'active':selectType===1}">{{desc.negative}} <span class="count">{{negative.length}}</span></span>
		</div>
		<div class="switch" @click="toggleContent" :class="{'on':onlyContent}">
			<span class="icon-check_circle"></span>
			<span class="text"></span>
		</div>
	</div>
</template>

<script>
	//3
	const Positive = 0;
	const Negative = 1;
	const All = 2;
	export default{
	    props:{
	        ratings:{
	            type:Array,
				default(){
	                return [];
				}
			},
			selectType:{
	            type:Number,
				default(){
	                return All;
				}
			},
			onlyContent:{
	            type:Boolean,
				default(){
	                return false;
				}
			},
			desc:{
	            type:Object,
				default(){
	                return {
	                    all:'',
						positive:'',
						negative:''
					}
				}
			}
		},
		computed:{
			positive() {
	      	    return this.ratings.filter( (item)=> {
	      	        return item.rateType ===  Positive;
				})
			},
			negative() {
				return this.ratings.filter( (item)=> {
					return item.rateType ===  Negative;
				})
			}
		},
		methods:{
	        select(type,event){
	            if(!event._constructed){
	                return;
				}
				// $emit  props   view 
//				this.selectType = type;
				this.$emit('select',type);
			},
			toggleContent(event){
	            if(!event._constructed){
	                return;
				}
				//  $emit ('xxx',arguments);   @xxx = 'aaa'     methods  aaa(arguments) 
	            this.$emit('onlyContent',this.onlyContent);
			}
		}
	}
</script>

<style lang="stylus" rel="stylesheet/stylus">

	@import "../../common/stylus/mixin.styl"
	.ratingselect{
		.rating-type{
			padding :18px 0
			margin:0 18px
			border-1px(rgba(7,17,27,0.1))
			font-size :0   //  display:inline-block  font-size:0 
			.block{
				display :inline-block
				padding :8px 12px
				/*border :1px solid red*/
				font-size :16px
				margin-right :8px
				border-radius :2px
				color:rgb(77,89,53)
				line-height :16px
				&.active{
					color :#fff
				}
				.count{
					font-size :8px
					margin-right :2px
				}
				&.positive{
					background-color :rgba(0,160,220,0.2)
					&.active{
						background-color :rgb(0,160,220)
					}
				}
				&.negative{
					background-color :rgba(77,85,93,0.2)
					&.active{
						background-color :rgb(77,85,93)
					}
				}

			}
		}
		.switch{
			padding :12px 18px
			color :rgb(147,153,159)
			line-height :24px;
			border-bottom 1px solid rgb(147,153,159)
			&.on {
				.icon-check_circle {
					color: #00c850
				}
			}
			.icon-check_circle{
				display :inline-block
				font-size :24px
				vertical-align :middle
			}
			.text{
				display :inline-block
				font-size :12px
			}
		}
	}

</style>



